* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'monseratt', sans-serif;
}

.app {
  background-image: url('./assets/cold-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition: background-image 0.4 ease-out;

  /* transition: background-image 0.4 ease-out; */
}

.app.warm {
  background-image: url(./assets/warm-bg.jpg);
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  padding: 25px;
}

/* Search box */
.search {
  width: 100%;
  margin: 0 0 75px;
}

.search__bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  transition: 0.4s ease;
}

.search__bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.search__error {
  display: flex;
  justify-content: center;
  align-items: center;

  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 50px;

  text-align: center;
  font-weight: 700;

  height: 50px;
  width: 150px;

  color: #fff;
  background-color: rgba(233, 64, 64, 0.2);
  box-shadow: 3px 6px rgba(0, 0, 0, 0.192);

  margin-top: 10px;
  border-radius: 16px;
}

/* location */

.location__city {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location__date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

/* weather box */

.weather {
  text-align: center;
}

.weather__temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;
  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather__temp-feelslike {
  color: #fff;
  font-size: 20px;
}

.weather__sky {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

/* details box */

.details {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  gap: 10px;

  color: #fff;

  padding-top: 20px;
}
.details-left,
.details-right {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;
  color: #fff;
  font-size: 32px;
  font-weight: 500;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);

  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}
